.ahref{padding: 8px 25px;color: #fff;transition: 0.3s;border: 1px solid #b9b9b9;margin-top: 30px;display: inline-block;}
.ahref:hover{background: #03A9F4;transition: 0.3s;}
.tabs_con_row{margin-top: 30px;color: #fff;}
