<style>
.fina_box{width: 100%; height: auto;}
.fina_ul{margin: 0; padding: 0; width: 100%;height: auto;}
.fina_li{height: 40px;width: 100%;line-height: 40px;font-size: 14px;color: #222;border-bottom: 1px dashed #ddd;  }
.fina_li label{cursor: pointer;}
.fina_p{color: #4C4B4B; font-size: 12px;margin-top: 10px;margin-bottom: 10px;}
.fina_lis{float: right;margin-right: 10px;}


</style>

<template>
<div class="fina_box">
	<h1></h1>
	<p class="fina_p"></p>
	 <ul class="fina_ul">
	 		<li class="fina_li" v-for="(item,index) in fina_data" @click="SetlocalStorage(index,true)" v-if="!item.checkboxs">
	 			<input type="checkbox" v-model="item.checkboxs" :id="index" /> <label    :for="index">{{item.title}}</label><span class="fina_lis">{{item.times}}</span></li>
	 </ul>
	 <h1></h1>
	<p class="fina_p"></p>
	 <ul class="fina_ul">
	 	  <li class="fina_li"  v-for="(item,index) in fina_data" @click="SetlocalStorage(index,false)"  v-if="item.checkboxs">
	 	  	<input type="checkbox" v-model="item.checkboxs" :id="index" /><label  :for="index">{{item.title}}</label><span class="fina_lis">{{item.times}}</span></li>
	 </ul>
</div>
</template>

<script>
	import setstorage from '../js/setLocalstorage.js'
	export default{
		data(){
			return{
				fina_data:[
					{times:"2019-05-13", checkboxs:false, title:""},
					{times:"2019-05-13", checkboxs:false, title:" "},
					{times:"2019-05-13", checkboxs:false, title:"  "},
					{times:"2019-05-13", checkboxs:false, title:"  "},
					{times:"2019-05-10", checkboxs:false, title:"  "},
					{times:"2019-05-09", checkboxs:false, title:"  "},
					{times:"2019-05-07", checkboxs:false, title:" "},
					{times:"2019-05-06", checkboxs:false, title:"    "},
					{times:"2019-05-06", checkboxs:false, title:"  "},
					{times:"2019-04-30", checkboxs:false, title:" "},
					{times:"2019-05-06", checkboxs:false, title:" "},
	                {times:"2019-04-30", checkboxs:false, title:""},
	                {times:"2019-04-30", checkboxs:false, title:""},
	                {times:"2019-04-30", checkboxs:false, title:" "},
	                {times:"2019-04-29", checkboxs:false, title:"423"},
	                {times:"2019-04-28", checkboxs:false, title:""},
	                {times:"2019-04-28", checkboxs:false, title:"  "},
	                {times:"2019-04-28", checkboxs:false, title:"  "},
	                {times:"2019-04-28", checkboxs:false, title:"   "},
	                {times:"2019-04-26", checkboxs:false, title:""}
				],
				checkboxs:false,
				
			}
		},
		methods:{
			SetlocalStorage:function(index,bool){
				this.fina_data[index].checkboxs=bool
					//sessionStorage.setItem("item", JSON.stringify(this.fina_data))
					setstorage.set_item('item',this.fina_data)
				}
		},
		mounted(){
			let SetlocalStorage=setstorage.get_item('item')
			 if(SetlocalStorage){
			 	this.fina_data=SetlocalStorage
			 }
		}
	}
</script>




