<template>
	<div class="loop_con">
		<div class="loop_con_left">
			<div class="loop_con_l_title">
				<h2>--{{box}}-{{bon.name}}</h2>
				<!-- <button @click="fns()"></button> -->
			    <!--<p></p>-->
			</div>
			<div class="loop_con_l_content">
				<span class="con_l_left" @click="loop_left"></span>
				<span class="con_l_right" @click="loop_right"></span>
			</div>
		</div>
		<div class="loop_con_right">
			<ul class="loop_con_pao" id="loop_con_pao_id" >
				<li @mouseover="li_mou" @mouseout="li_out" v-for="(items,index) in con_paoData" :key="index"><div class="loop_con_logo"><img src="../../build/logo.png"/></div><p class="loop_con_title">{{items.title}}</p><div class="loop_con_title_text">{{items.text}}</div></li>
			<!--<transition-group   class="loop_con_pao" id="loop_con_pao_id" name="fadeIn" mode="out-in" tag="ul" >
				<li @mouseover="li_mou" @mouseout="li_out" v-for="(items,index) in con_paoData" :key="index"><div class="loop_con_logo"><img src="../../build/logo.png"/></div><p class="loop_con_title">{{items.title}}</p><div class="loop_con_title_text">{{items.text}}</div></li>
			</transition-group>-->
			</ul>
		</div>
	</div>
</template>

<script>
	export default({
		data(){
			return{
				 con_paoData:[
				 	{title:"ISO27", text:""},
				 	{title:"ISO29", text:"ISO27018 ISO 27018"},
				 	{title:"CSA STAR", text:" ITSSITSSITIT"},
				 	{title:"", text:"ISO27001ISO 27001"},
				 	{title:"ITSS", text:"CSA STARCCM"},
				 	{title:"PCI", text:"ISO20000ISO 20000IT(ITSM)IT"},
				 	{title:"ISO20", text:"ISO27017 ISO 27017"},
				 	{title:"ISO22", text:"PCI-DSS"},
				 	{title:"ISO27017 ", text:"ISO22301ISO 22301BCM"},
				 	{title:"BS10012 ", text:"ISO 9001 BS 5750BSIISO 9001"},
				 	{title:"ISO9001", text:"BS 10012:2009GDPRBS 10012:2017GDPRBS 10012"},
				 	{title:"ISO2966", text:"ISO/IEC29151:2017(PII)"},
				 ],
				 loop_ser:null,
			}
		},
		created(){
			//this.setINfn()
		},
		props:["box","bon",'fns'],
		methods:{
			 loop_left:function(){ },
			 loop_right:function(){ },
			 li_mou:function(){
			 	//
			 	 	clearInterval(this.loop_ser);
			 	 	this.loop_ser=null
			 },
			 li_out:function(){
			 	//
			 	//this.setINfn()
			 },
			 setINfn:function(){
			 	 this.loop_ser = setInterval(()=>{
			 	var did =document.getElementById("loop_con_pao_id")
			 	var dli =did.getElementsByTagName("li");
			 	dli[0].style.marginLeft=-230+"px";
	 			//func_gets()
			 },3000);
			function func_gets(){
				var bins=[];
				var box_ul =document.getElementById("loop_con_pao_id");
				var a_li =box_ul.getElementsByTagName("li");
				var apli = document.createElement("li");
					var loop_ser2 =setTimeout(()=>{
						a_li[0].removeAttribute("style")
						bins.push(a_li[0]);
						box_ul.removeChild(a_li[0]);
						box_ul.appendChild(bins[0]);
					},1000)	
			    }
			 }
		}
	})
</script>

<style>
	
	
	
	.loop_box{width: 100%;height: auto;background: #fff;margin-top: 20px;padding:20px 0;}
	.loop_con{overflow: hidden;height: 320px;}
	.loop_con_left{float: left;width: 300px;height: 100%;}
	.loop_con_l_title{height: 150px;margin-top: 100px;margin-left: 20px;margin-right: 20px;}
	.loop_con_l_title h2{font-weight: inherit;font-family: "";font-size: 24px;margin-bottom: 15px; color: #2B2B2B;}
	.loop_con_l_title p{font-size: 14px;color: #777;line-height: 30px;}
	.loop_con_l_content{height:60px;width: 300px;margin-left: 50px;}
	.loop_con_l_content span{display: inline-block;width: 100px;cursor: pointer; height: 50px;border: 1px solid #ddd; position: relative;}

	.con_l_left:before{display: block;content: ''; border: 15px #ddd solid;width: 0px;transform: rotateZ(45deg);position: absolute;top: 10px;left: 42px;}
	.con_l_left:after{display: block;content: ''; border: 15px #fff solid;width: 0px;transform: rotateZ(45deg);position: absolute;top: 10px;left: 48px}
	.con_l_right:before{display: block;content: ''; border: 15px #ddd solid;width: 0px;transform: rotateZ(45deg);position: absolute;top: 10px;left: 28px;}
	.con_l_right:after{display: block;content: ''; border: 15px #fff solid;width: 0px;transform: rotateZ(45deg);position: absolute;top: 10px;left: 22px}
	
	.loop_con_right{float: left;width:calc(100% - 310px); height: 100%; overflow: hidden;}
	.loop_con_pao{width:10000px;height: 100%;padding-top: 10px;box-sizing: border-box;}
	.loop_con_pao li{height: 300px;width: 215px; box-sizing: border-box;box-shadow: #ccc 0px 2px 10px 1px; margin:0 8px; float: left;padding: 15px;transition: 0.5s;}
	.loop_con_logo{width: 100%;text-align: center;}
	.loop_con_logo img{width: 65px;margin: 0 auto;}
	.loop_con_title{margin-bottom: 15px;text-align: center;}
	
	
	.fadeIn-enter-active,.fadeIn-leave-active {
	  transition: all 1s ease;
	}
	.fadeIn-enter-active,.fadeIn-leave{
	    opacity: 1;
	}
	.fadeIn-enter,.fadeIn-leave-active {
	  opacity: 0;
	}
</style>


