import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/components/home_page'//
import NewsPage from '@/components/news_page'//
import Newslist from '@/components/news_list_con'// 
import NeweStlist from '@/components/newest_list'//
import Market from '@/components/market'//
import Business from '@/components/business'//
import Finance from '@/components/finance'//
 

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: homePage
    },{
      path: '/home',
      name: 'home',
      component: homePage
    },
    {
      path: '/news',
      name:'news',
      component: NewsPage,
    },
     {//query   query 
      path:"/news_list_con", 
      name: 'news_list_con',
      component: Newslist
    },
    /*{//params 
      path:"/news/news_list_con:/id/:title",
      name: 'news_list_con',
      component: Newslist
    },*/
   {
   	path:'/newstlist',
   	name:'newstlist',
   	component:NeweStlist
   },
    {
   	path:'/market',
   	name:'market',
   	component:Market
   },
   {
   	path:'/business',
   	component:Business
   },
   {
   	path:'/fina',
   	name:'fina',
   	component:Finance
   }
  ]
})



